


































import { Component, Vue } from "vue-property-decorator";
import ProForm from "@/components/pro-form/pro-form.vue";
import { DictionaryItem } from "@/mode/dictionary-item";
@Component({
    components: { ProForm }
})
export default class ProFormtEXT extends Vue {
    labelPosition = "right";
    config = {
        labelWidth: "120",
        mode: "read",
        column: 3
    };
    formData = {
        cascader: "",
        firstname: "",
        age: 19,
        sex: 1,
        memo: "描述信息",
        money: 100,
        color: "#f40",
        second: +new Date(),
        percent: 100,
        rate: 5,
        radio: 1,
        checkbox: [1, 2],
        process: 80,
        password: "password",
        date: "2021-5-12",
        datetime: "2021-5-12 12:48:48",
        dateRange: ["2021-5-12", "2021-5-13"],
        datetimeRange: ["2021-5-12 12:45:30", "2021-5-13 12:54:03"],
        time: "12:56:56",
        timeRange: ["12:56:56", "23:23:23"],
        week: "2021-5-12",
        image: "https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png",
        custome: "自定义插槽"
    };
    saveHandle() {
        (this.$refs["pro-from"] as ProForm).validate((valid: boolean) => {
            console.log(valid ? "校验通过" : "校验失败");
        });
    }
    get formlist() {
        return [
            {
                title: "表单一",
                labelWidth: this.config.labelWidth,
                tooltip: "这个是测试水平表单",
                column: this.config.column,
                mode: this.config.mode,
                items: [
                    {
                        type: "slot",
                        label: "插槽",
                        prop: "custome"
                    },
                    {
                        type: "cascader",
                        label: "级联",
                        prop: "cascader",
                        options: this.cascaderList
                    },
                    {
                        type: "text",
                        label: "文本框",
                        prop: "firstname",
                        tooltip: "这个是姓名",
                        other: {
                            tip: '温馨提示: 你好！<span style="color:#f40">hello world</span>'
                        },
                        rules: [
                            {
                                validator: this.validator
                            }
                        ]
                    },
                    {
                        type: "digit",
                        label: "数字",
                        prop: "age",
                        tooltip: "年龄"
                    },
                    {
                        type: "money",
                        label: "金额",
                        prop: "money"
                    },
                    {
                        type: "color",
                        label: "颜色",
                        prop: "color"
                    },
                    {
                        type: "second",
                        label: "秒",
                        prop: "second",
                        rules: [
                            {
                                required: true,
                                message: "请输入数字"
                            }
                        ]
                    },
                    {
                        type: "percent",
                        label: "百分比",
                        prop: "percent"
                    },
                    {
                        type: "rate",
                        label: "评分",
                        prop: "rate"
                    },
                    {
                        type: "select",
                        label: "下拉框",
                        prop: "sex",
                        tooltip: "性别",
                        options: [new DictionaryItem(1, "男"), new DictionaryItem(2, "女")]
                    },
                    {
                        type: "radio",
                        label: "单选框",
                        prop: "radio",
                        options: [new DictionaryItem(1, "开心"), new DictionaryItem(2, "郁闷")]
                    },
                    {
                        type: "checkbox",
                        label: "复选框",
                        prop: "checkbox",
                        options: [
                            new DictionaryItem(1, "篮球"),
                            new DictionaryItem(2, "游戏"),
                            new DictionaryItem(3, "看书")
                        ]
                    },
                    {
                        type: "process",
                        label: "进度条",
                        prop: "process"
                    },
                    {
                        type: "password",
                        label: "密码",
                        prop: "password"
                    },
                    {
                        type: "date",
                        label: "日期选择",
                        prop: "date"
                    },
                    {
                        type: "time",
                        label: "时间选择",
                        prop: "time"
                    },
                    {
                        type: "week",
                        label: "第几周选择",
                        prop: "week"
                    },
                    {
                        type: "datetime",
                        label: "日期时间",
                        prop: "datetime"
                    },
                    {
                        type: "dateRange",
                        label: "日期区间",
                        prop: "dateRange"
                    },
                    {
                        type: "image",
                        label: "图片",
                        prop: "image",
                        srcList: [
                            "https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png",
                            "https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg",
                            "https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg"
                        ]
                    },
                    {
                        type: "textarea",
                        label: "描述",
                        prop: "memo",
                        tooltip: "描述"
                    },
                    {
                        type: "datetimeRange",
                        label: "日期时间区间",
                        prop: "datetimeRange"
                    }
                ]
            },
            {
                title: "表单二",
                labelWidth: 120,
                tooltip: "这个是测试水平表单",
                column: 1,
                mode: this.config.mode,
                items: [
                    {
                        type: "text",
                        label: "名称",
                        prop: "lastname",
                        rules: [
                            {
                                required: true,
                                message: "请输入姓名",
                                trigger: "blur"
                            }
                        ]
                    }
                ]
            }
        ];
    }
    cascaderList = [
        {
            value: "zhinan",
            label: "指南",
            children: [
                {
                    value: "shejiyuanze",
                    label: "设计原则",
                    children: [
                        {
                            value: "yizhi",
                            label: "一致"
                        },
                        {
                            value: "fankui",
                            label: "反馈"
                        },
                        {
                            value: "xiaolv",
                            label: "效率"
                        },
                        {
                            value: "kekong",
                            label: "可控"
                        }
                    ]
                },
                {
                    value: "daohang",
                    label: "导航",
                    children: [
                        {
                            value: "cexiangdaohang",
                            label: "侧向导航"
                        },
                        {
                            value: "dingbudaohang",
                            label: "顶部导航"
                        }
                    ]
                }
            ]
        },
        {
            value: "ziyuan",
            label: "资源",
            children: [
                {
                    value: "axure",
                    label: "Axure Components"
                },
                {
                    value: "sketch",
                    label: "Sketch Templates"
                },
                {
                    value: "jiaohu",
                    label: "组件交互文档"
                }
            ]
        }
    ];
    validator(rule: any, value: any, callback: any) {
        return value >= 10 || value <= 0 ? callback("请输入0~10之间") : callback();
    }
}
